<template>
  <div id="group-loading-container" class="vs-con-loading__container">
    <div class="inline-form p-4 mb-base" v-if="userGroupFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col w-full required">
          <label class="vs-input--label">{{ $t('USER_GROUP') }}</label>
          <user-group-lookup :title="$t('USER_GROUP')" valueField="userGroupId" displayField="description" @select-record="selectUserGroupRecord"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('FROM_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="fromDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('TO_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="toDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
      </div>
      <div class="nv-row mb-4" v-if="wiki">
        <div class="nv-col w-full">
          <div class="code-editor bg-white p-4">
            <markdown-it-vue :content="wiki"/>
          </div>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col sm:w-1/2 w-full">
          <div class="flex justify-left">
            <vs-button class="px-5 py-3 mr-2" @click="createUserGroupMembership" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
            <vs-button class="px-5 py-3 mr-2" @click="hideUserGroupForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
          </div>
        </div>
      </div>
    </div>

    <vs-table class="mb-base" :data="userGroupTableData" :noDataText="userGroupTableNoDataText" :sst="true" search @search="filterUserGroupTable" @sort="orderUserGroupTable">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="showUserGroupForm" :disabled="userGroupFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('ADD') }}</vs-button>
          <vs-button class="p-3 mr-4" @click="refreshUserGroupTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="userGroupTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ userGroupTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="userGroupTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="userGroupId">
          {{ this.$t('ID') }}
        </vs-th>
        <vs-th sort-key="userGroupDescription">
          {{ this.$t('GROUP_NAME') }}
        </vs-th>
        <vs-th sort-key="fromDate">
          {{ this.$t('FROM_DATE') }}
        </vs-th>
        <vs-th sort-key="thruDate">
          {{ this.$t('TO_DATE') }}
        </vs-th>
        <vs-th v-if="$acl.check('isNucleusAll')">
          {{ this.$t('OPTIONS') }}
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].userGroupId">
            <router-link :to="{ name: 'ViewUserGroup', params: { id: data[index].userGroupId } }">{{ data[index].userGroupId }}</router-link>
          </vs-td>
          <vs-td :data="data[index].userGroupDescription">
            <span :class="disabledClass(data[index])">{{ data[index].userGroupDescription }}</span>
          </vs-td>
          <vs-td :data="data[index].fromDate">
            {{ data[index].fromDate | formatUnixDate }}
          </vs-td>
          <vs-td :data="data[index].thruDate">
            {{ data[index].thruDate | formatUnixDate }}
          </vs-td>
          <vs-td :data="data[index].options" v-if="$acl.check('isNucleusAll')">
            <vs-button class="p-3 mr-4" color="danger" @click="expireUserGroupMembership(data[index])">{{ $t('EXPIRE') }}</vs-button>
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="userGroupTableTotalPages" v-model="userGroupTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
  </div>
</template>

<script>
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import UserGroupTableMixin from '@/mixins/nucleus/UserGroupTableMixin'
import SecurityService from '@/services/nucleus/SecurityService'
import UserGroupLookup from '@/views/nucleus/user-group/UserGroupLookup'
import MarkdownItVue from 'markdown-it-vue'
import Datepicker from 'vuejs-datepicker'

export default {
  name: 'user-group-membership',
  mixins: [DatePickerMixin, LoadingMixin, ListLayoutMixin, UserGroupTableMixin],
  components: {
    UserGroupLookup,
    MarkdownItVue,
    Datepicker
  },
  props: {
    userId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      userGroupTableData: [],
      userGroupTableFilter: null,
      userGroupTableFilterTimer: null,
      userGroupTableOrderBy: null,
      userGroupTableContainer: 'group-loading-container',
      userGroupTableBusy: false,
      userGroupTableTotalRows: 0,
      userGroupTableCurrentPage: 1,
      userGroupTablePerPage: 10,
      userGroupFormVisible: false,
      userGroupId: null,
      wiki: null,
      fromDate: null,
      toDate: null
    }
  },
  computed: {
    userGroupTableNoDataText: function () {
      return this.userGroupTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    userGroupTableTotalPages: function () {
      return Math.ceil(this.userGroupTableTotalRows / this.userGroupTablePerPage)
    },
    userGroupTablePagingInfo: function () {
      return this.pagingInfo(this.userGroupTableCurrentPage, this.userGroupTablePerPage, this.userGroupTableTotalRows)
    }
  },
  methods: {
    loadUserGroupTableData (loadingContainerId) {
      let self = this
      this.userGroupTableBusy = true
      this.userGroupTableContainer = loadingContainerId || this.userGroupTableContainer
      this.startLoading(this.userGroupTableContainer)
      SecurityService.userGroupMemberships({
        pageIndex: this.userGroupTableCurrentPage - 1,
        pageSize: this.userGroupTablePerPage,
        filter: this.userGroupTableFilter,
        orderByField: this.userGroupTableOrderBy,
        userId: this.userId
      }).then(function (response) {
        self.stopLoading(self.userGroupTableContainer)
        self.userGroupTableBusy = false
        self.userGroupTableTotalRows = response.data.totalRows
        self.userGroupTableData = response.data.userGroupMemberList || []
      })
    },
    refreshUserGroupTable () {
      this.loadUserGroupTableData()
    },
    filterUserGroupTable (filter) {
      let self = this
      if (this.userGroupTableFilter !== filter) {
        clearTimeout(this.userGroupTableFilterTimer)
        this.userGroupTableFilterTimer = setTimeout(() => {
          self.userGroupTableFilter = filter
          self.loadUserGroupTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.userGroupTableFilterTimer)
      }
    },
    orderUserGroupTable (orderBy, active) {
      this.userGroupTableOrderBy = (active ? '' : '-') + orderBy
      this.loadUserGroupTableData()
    },
    showUserGroupForm () {
      this.userGroupFormVisible = true
    },
    hideUserGroupForm () {
      this.userGroupFormVisible = false
      this.userGroupId = ''
      this.wiki = ''
    },
    selectUserGroupRecord (record) {
      this.userGroupId = record.userGroupId || ''
      this.wiki = record.wiki || ''
    },
    createUserGroupMembership () {
      let self = this
      SecurityService.createUserGroupMembership({
        userGroupId: this.userGroupId,
        userId: this.userId,
        fromDate: this.getTime(this.fromDate),
        toDate: this.getTime(this.toDate)
      }).then(function (response) {
        self.refreshUserGroupTable()
        self.hideUserGroupForm()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    expireUserGroupMembership (user) {
      let self = this
      SecurityService.expireUserGroupMembership({
        userGroupId: user.userGroupId,
        userId: user.userId,
        fromDate: user.fromDate
      }).then(function (response) {
        if (response.status === 200) {
          self.refreshUserGroupTable()
        }
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    }
  },
  mounted () {
    this.loadUserGroupTableData()
  },
  watch: {
    userGroupTableCurrentPage: function () {
      this.refreshUserGroupTable()
    },
    userGroupTablePerPage: function () {
      this.refreshUserGroupTable()
    }
  }
}
</script>
