<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
      <vs-table class="mb-base" :data="userGroupTableData" :noDataText="userGroupTableNoDataText" :sst="true" search @search="filterUserGroupTable" @sort="orderUserGroupTable" @selected="selectRow">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="loadUserGroupTableData(popupRef)" type="filled" icon="fa-sync" icon-pack="far" :disabled="userGroupTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ userGroupTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="userGroupTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="userGroupId">
            {{ this.$t('ID') }}
          </vs-th>
          <vs-th sort-key="description">
            {{ this.$t('GROUP_NAME') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].userGroupId">
              {{ data[index].userGroupId }}
            </vs-td>
            <vs-td :data="data[index].description">
              <span :class="disabledClass(data[index])">{{ data[index].description }}</span>
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>

      <vs-pagination :total="userGroupTableTotalPages" v-model="userGroupTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </div>
  </vs-popup>
</template>

<script>
import PopupMixin from '@/mixins/commons/PopupMixin'
import UserGroupTableMixin from '@/mixins/nucleus/UserGroupTableMixin'

export default {
  name: 'user-group-popup',
  mixins: [PopupMixin, UserGroupTableMixin],
  mounted: function () {
    this.loadUserGroupTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
