<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="flex flex-wrap-reverse items-center mb-base">
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="modify" color="primary" type="filled" icon="fa-pen" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('MODIFY') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="approveUser" color="primary" type="filled" icon="fa-stamp" icon-pack="far" v-if="$acl.check('isNucleusAll') && user.approved === 'N'">{{ $t('APPROVE') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="enableUser" color="primary" type="filled" icon="fa-check" icon-pack="far" v-if="$acl.check('isNucleusRescue') && user.disabled === 'Y'">{{ $t('ENABLE') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="disableUser" color="primary" type="filled" icon="fa-ban" icon-pack="far" v-if="$acl.check('isNucleusRescue') && user.disabled === 'N'">{{ $t('DISABLE') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="resetPassword" color="primary" type="filled" icon="fa-key" icon-pack="far" v-if="$acl.check('isNucleusRescue')">{{ $t('RESET_PASSWORD') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="forceLogout" color="primary" type="filled" icon="fa-sign-out" icon-pack="far" v-if="$acl.check('isNucleusRescue')">{{ $t('FORCE_LOGOUT') }}</vs-button>
      </div>
      <div class="vs-row flex hover mb-base">
        <div class="nv-col sm:w-1/2 w-full p-2">
          <p class="info-row">
            <span>{{ $t('ID') }}:</span>
            <span>{{ user.userId }}</span>
          </p>
          <p class="info-row" v-if="user.partyId">
            <span>{{ $t('PARTY_ID') }}:</span>
            <span><router-link :to="{ name: 'ViewParty', params: { id: user.partyId } }">{{ user.partyId }}</router-link></span>
          </p>
          <p class="info-row">
            <span>{{ $t('USERNAME') }}:</span>
            <span :class="disabledClass(user)">{{ user.username }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('CREATION_DATE') }}:</span>
            <span>{{ user.creationDate | formatUnixTimestamp }}</span>
          </p>
        </div>
        <div class="nv-col sm:w-1/2 w-full p-2">
          <p class="info-row">
            <span>{{ $t('APPROVED') }}:</span>
            <span>{{ user.approved | prettyBool }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('APPROVED_BY') }}:</span>
            <span>{{ user.approvedBy | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('APPROVAL_DATE') }}:</span>
            <span>{{ user.approvalDate | formatUnixTimestamp }}</span>
          </p>
        </div>
      </div>

      <vs-tabs>
        <vs-tab :label="$t('USER_GROUPS')">
          <user-group-membership @show-alert="showAlert" :userId="userId"/>
        </vs-tab>
        <vs-tab :label="$t('ACL')">
          <user-acl @show-alert="showAlert" :userId="userId"/>
        </vs-tab>
      </vs-tabs>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import SecurityService from '@/services/nucleus/SecurityService'
import UserGroupMembership from '@/views/nucleus/user/user-view/UserGroupMembership'
import UserAcl from '@/views/nucleus/user/user-view/UserAcl'

export default {
  name: 'user-view',
  mixins: [AlertMixin, DisabledClassMixin],
  components: {
    UserGroupMembership,
    UserAcl
  },
  data: function () {
    return {
      user: {},
      userId: this.$route.params.id,
      popupActive: false
    }
  },
  methods: {
    async load () {
      let self = this
      SecurityService.users({
        userId: this.userId
      }).then(function (response) {
        self.user = response.data.userList[0] || {}
      })
    },
    approveUser () {
      let self = this
      SecurityService.approveUser({
        userId: this.userId
      }).then(function (response) {
        self.showSuccessAlert(response.data.messages)
        self.load()
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    enableUser () {
      let self = this
      SecurityService.enableUser({
        userId: this.userId
      }).then(function (response) {
        self.showSuccessAlert(response.data.messages)
        self.load()
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    disableUser () {
      let self = this
      SecurityService.disableUser({
        userId: this.userId
      }).then(function (response) {
        self.showSuccessAlert(response.data.messages)
        self.load()
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    forceLogout () {
      let self = this
      SecurityService.forceLogoutUser({
        userId: this.$route.params.id
      }).then(function (response) {
        self.showSuccessAlert(response.data.messages)
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    },
    modify () {
      this.$router.push({ name: 'ModifyUser', params: { id: this.$route.params.id } })
    },
    resetPassword () {
      this.$router.push({ name: 'ResetUserPassword', params: { id: this.$route.params.id } })
    }
  },
  mounted () {
    this.load()
  }
}
</script>
